import React from 'react';
import { FaDribbble } from 'react-icons/fa';
import styled from 'styled-components';

const P = styled.p`
    font-size: 16px;
    line-height: 20px;
    color: #0C7B93;
    opacity: 0.4;
    a {
        text-dacuration: none;
        color: inherit;
        &:hover {
            text-dacuration: underline;
        }
    }
`;
const CareerStatus = props => {
    const { companyName, statusText, webLink } = props;
    return (
        <>
            <h4>{companyName}</h4>
            <P className="mb-0">{statusText}</P>
            <P><a href={webLink} target="__blank" ><FaDribbble /> {webLink.split('//')[1]}</a></P>
        </>
    );
}

export default CareerStatus;
