import React from 'react';
import CareerStatus from './CareerStatus';
import CareerContent from './CareerContent';

const CareerRow = props => {
    const { companyName, statusText, content, webLink, position } = props;
    return (
        <div className="row mt-5 pt-lg-3">
            <div className="col-md-4">
                <CareerStatus companyName={companyName} statusText={statusText} webLink={webLink} />
            </div>
            <div className="col-md-8" >
                <CareerContent>
                    <h4>{position}</h4>
                    <div className="w100" dangerouslySetInnerHTML={{ __html: content }}></div>
                </CareerContent>
            </div>
        </div>
    );
}

export default CareerRow;
