import React from 'react';
// import "../../assets/styles/SectionTitle.scss";
import styled from 'styled-components';
const Title = styled.h2`
display: flex;
    align-items: center;
    width: 100%;
    font-size: 30px;
    letter-spacing: 0.265em;
    font-weight: 500;
    line-height: 35px;
    white-space: nowrap;
    color: #0c7b93;
    text-transform: uppercase;

    .bar {
        height: 5px;
        background: #0C7B93;
        display: inline-flex;
        flex: 1 0 auto;
        margin-left: 20px;
        border-radius: 2px;
    }

    @media screen and (max-width: 1450px) {
        font-size: 24px;
        line-height: 30px;
    }
    @media screen and (max-width: 450px) {
        letter-spacing: 1px;
    }
`;

const SectionTitle = props => {
    return (
        <Title>{props.title} <span className="bar"></span></Title>
    );
}

export default SectionTitle;
