import React from 'react';

const IframeVideo = (props) => {
    return (
        <div className="embed-responsive embed-responsive-16by9">
            <iframe className="iframde w-100 embed-responsive-item" width="560" height="315" src={props.url} allowfullscreen autoplay="off" loop></iframe>
        </div>
    );
}

export default IframeVideo;
