import React, { useState, useEffect } from 'react';
import SectionTitle from '../components/public/SectionTitle';
import CareerRow from '../components/public/CareerRow';

const Careers = props => {

    const [careerItems, setCareerItems] = useState(
        [{
            id: 3,
            companyName: "Brain Station 23 Ltd",
            webLink: 'https://brainstation-23.com',
            statusText: "Dec 2018-Continuing ",
            position: 'Front-end & UI Engineer',
            content: `<p>Develop and Maintain software using Angular, ReactJs, Javascript also abale to develop MS-Sharepoint based web application using ReactJS. Build responsive, pixel-perfect, cross-browser compatible websites from PSD, AI, PDF or any other documents. Modify the existing code as per client requirements. Support indivudal/team work.</p>`
        }, {
            id: 4,
            companyName: "ConstantMD",
            statusText: "Sep 2018-Nov 2018",
            webLink: 'https://Constantmd.com',
            position: "Web Frontend developer",
            content: `
                <ul class="pl-4">
                    <li>Modify existing code as needed.</li>
                    <li>Develop Angular apps.</li>
                    <li>Google-Page And Y-Slow Speed Optimize.</li>
                    <li>Any Project As Client Requirements</li>
                    <li>Project structure plan and build  prototype</li>
                    <li>Indivudal client support and requirement analysis</li>
                    <li>Develop and maintain Wordpress sites</li>
                </ul>`
        }, {
            id: 5,
            companyName: "Baseonesolutions",
            webLink: 'https://baseonesolutions.com',
            statusText: "Nov 2015-Sep 2018",
            position: "Web Frontend developer",
            content: `<p>Creative and dependable Front-end developer with 4+ years of experience. Comprehensive understanding of modern technologies such as Reactjs, Angular-4, HTML-5, CSS-3, Bootstrap 3,4, Foundation, Sass, jQuery and all of them are pixel-perfect UI design, standard coding, Cross browser compatible and easily editable from the admin panel.</p>`
        }]
    );

    return (
        <>
            <SectionTitle title="Careers" />
            <div className="pt-md-4">
                {
                    careerItems.map(careerItem =>
                        <CareerRow key={careerItem.id} companyName={careerItem.companyName} statusText={careerItem.statusText} webLink={careerItem.webLink} position={careerItem.position} content={careerItem.content} />
                    )
                }
            </div>
        </>
    );
}

export default Careers;
