import React from 'react';
import {
    Wrapper,
    Row,
    WrapperCenter,
    WrapperFulid,
} from '../components/public';
import About from './About';
import Careers from './Careers';

const Home = () => {
    return (
        <>
            <Wrapper>
                <Row>
                    <div className="col-12">
                        <section className="section" id="about-me">
                            <About />
                        </section>
                        <section className="section pt-0" id="section-2">
                            <Careers />
                        </section>
                        <section className="section" id="section-3">section 3</section>
                    </div>
                </Row>
            </Wrapper>
        </>
    );
}

export default Home;
