import React from 'react';
import aboutImage from '../../assets/images/slider-image-3.png';
import styled from 'styled-components';

const AboutCircle = styled.div`
    width: 260px;
    height: 260px;
    min-width: 260px;
    border-radius: 50%;
    box-shadow: 0px 4px 15px #c2dee4;
    overflow: hidden;
    padding: 15px;
    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-shadow: 0 0 5px rgba($color: #000000, $alpha: 0.1) inset;
        z-index: 10;
        border-radius: 50%;
    }

    img {
        width: 100%;
        border-radius: 50%;
    }
`;
const CircleImage = () => {
    return (
        // <div className="about-circle">
        //     <img className="img-fluid" src={aboutImage} />
        // </div>

        <AboutCircle className="about-circle">
            <img className="img-fluid" src={aboutImage} />
        </AboutCircle>
    );
}

export default CircleImage;
